import { daysInMonth } from './constants';

export const convertToFullYear = input => {
  const today            = new Date();
  const currentYear      = today.getFullYear();
  const currentCentury   = parseInt(currentYear.toString().substr(0, 2)) * 100;
  const currentShortYear = parseInt(currentYear.toString().substr(2));
  const shortYear        = parseInt(input);

  return shortYear > currentShortYear ? currentCentury - 100 + shortYear : currentCentury + shortYear;
};

export const getCookie = () => {
  const cookies = document.cookie.split('; ');
  let user = {
    isAdmin: false,
    isSignedIn: false,
    md5: null,
    name: null
  };

  cookies.forEach(cookie => {
    const parts = cookie.split('=');
    const key   = parts[0];
    const value = parts[1];

    if (key === 'user' && value !== '') {
      user = JSON.parse(decodeURIComponent(value));

      setCookie('user', JSON.stringify(user));
    }
  });

  return user;
}

export const getDaysInMonth = (month, year) => {
  return month === 2 && year % 4 === 0 ? 29 : daysInMonth[month - 1];
};

export const padDate = input => {
  return input.toString().padStart(2, '0');
};

export const setCookie = (name, value, expirationDate, path = '/') => {
  if (typeof expirationDate === 'undefined') {
    expirationDate = new Date();
    expirationDate.setHours(expirationDate.getHours() + 24);
  }

  document.cookie = `${name}=${value}; expires=${expirationDate}; path=${path}`;
}
